import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-loremipsum",
  templateUrl: "./loremipsum.component.html",
  styleUrls: ["./loremipsum.component.css"]
})
export class LoremipsumComponent implements OnInit {
  name = "";
  cls = "colorGreen";
  defaultValue = "";
  defaultPlaceHolder = "default placeholder  hai";
  times = 0;
  taskList = [];
  tasks = '';
  totalTaskCount=0

  show:boolean=true;
  showView:number=0;
  constructor() {}

  ngOnInit() {}
  increaseCount() {
    this.times++;
  }
  addTask() {
    if(this.tasks!=''){
    this.taskList.push(this.tasks);
    this.tasks = '';
    this.totalTaskCount=this.taskList.length;
    }
  }
  removeTask(index) {
    this.taskList.splice(index,1);
    this.tasks = '';
    this.totalTaskCount=this.taskList.length;
  }
  onChangeEvent()
  {
    this.show=!this.show;
  }
}



